<template>
  <div>
      <div class="fromBox">
        <el-form label-position="right" :rules="rules" label-width="100px" ref="formLabelAlign" :model="formLabelAlign">
            <el-form-item label="采集设备id" prop="cd_nodeid">
                <el-input v-model.number="formLabelAlign.cd_nodeid"></el-input>
            </el-form-item>
            <el-form-item label="设备名称" prop="cd_name">
                <el-input v-model="formLabelAlign.cd_name"></el-input>
            </el-form-item>
            <el-form-item label="出厂日期" prop="cd_produce_time">
                <el-input v-model="formLabelAlign.cd_produce_time"></el-input>
            </el-form-item>
            <el-form-item label="设备生产商" prop="cd_manufacturer">
                <el-input v-model="formLabelAlign.cd_manufacturer"></el-input>
            </el-form-item>
            <el-form-item>
                <el-button type="primary" @click="submitForm('formLabelAlign')">{{this.$route.query.type}}</el-button>
            </el-form-item>
        </el-form>
      </div>
  </div>
</template>

<script>
import { mapState } from "vuex";
import url from "../../common/api";
export default {
    data(){
        return{
          formLabelAlign: {
              cd_nodeid: '',
              cd_name: '',
              cd_produce_time: '',
              cd_manufacturer: '',
          },
          rules: {
            cd_nodeid: [
              { required: true, message: '请输入设备id', trigger: 'blur' },
              { type:'number', message: '请输入数字', trigger: 'blur' }
            ],
            cd_name: [
              { required: true, message: '请输入设备名称', trigger: 'blur' }
            ],
            cd_produce_time: [
              { required: true, message: '请输入设备出场日期', trigger: 'blur' }
            ],
            cd_manufacturer: [
              { required: true, message: '请输入设备生产商', trigger: 'blur' }
            ],
          }
        };
    },
    methods:{
      submitForm(formName) {
        console.log(this.formLabelAlign,'------------看见')
        this.$refs[formName].validate((valid) => {
          if (valid) {
            if(this.$route.query.type=='修改'){

            }else{
              this.formLabelAlign.company_id=this.$route.query.company_id
              this.formLabelAlign.project_id=this.$route.query.project_id
              this.formLabelAlign.user_id=this.buserInfo.user_id
              url.applycollectdevice(this.formLabelAlign).then(res=>{
                console.log(res,'--------------研华登陆接口')
                if(res.data.code){
                  this.$message({ 
                    message: res.data.message[0],
                    type: 'success'
                  });
                }else{
                  this.$message({ 
                    message: '添加失败，请检查信息重新修改',
                    type: 'error'
                  });
                }
              })
            }

          } else {
            console.log('error submit!!');
            return false;
          }
        });
      },
      Fncollectdevicelist(){
        url.collectdevicelist({
          "project_id": this.$route.query.project_id,
          "user_id": this.buserInfo.user_id
        }).then(res=>{
          console.log(res,'-----------列表')
          let xx=res.data.data[0].fields
          xx.collectdevice_id=res.data.data[0].pk
          this.formLabelAlign=xx
        })
      },
    },
    mounted(){
      if(this.$route.query.type=='修改'){
        this.Fncollectdevicelist()
      }

    },
    computed:{
      ...mapState(['buserInfo'])
    }
}
</script>

<style>

</style>


